body {
  background-color: darkgoldenrod;
  height:100vh;
  width:100vw;
  overflow-y:auto;
  overflow-x:hidden;
  touch-action: pan-y;
  user-select: none;
}

.list-item {
  /* width:100%; */
  height:50px;
  margin: 2px 2px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: lightgray;
}

.drag-style {
  background-color: orange;
}